.read-question-main {
    padding: 40px;
    font-family: 'Poppins', sans-serif;
    color: #333;

    .read-question-heading {
        font-size: 34px;
        font-weight: bolder;
        color: #04724d;
        text-shadow: 0px 1px, 1px 0px, 1px 1px;
        letter-spacing: 1.5px;
    }

    .read-question-body {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .read-question-time-left {
        font-size: 12px;
        padding: 20px;
        font-weight: 600;
        opacity: 0.8;
    }

    .read-question-ques-div{
        width: 885px;
        background: #fff;
        margin-top: 30px;
        padding: 20px 20px 50px 20px;
        border-radius: 8px;
    }

    .ques-wrapper{
        display: flex;
        align-items:'center'
    }

    .question-number{
        margin-right: 20px;
        font-size: 15px;
        font-weight: 600;
    }

    .question-text-new{
        font-size: 15px;
        font-weight: 600;
    }

    .timer-new{
        display: block !important;
        margin: 0 15px !important;
    }
}
